* {
  box-sizing: border-box;
  font-family: Georgia, "Times New Roman", Times, serif;
}
.App {
  margin: 0;
  padding: 0;
  background-color: #ced3e7;
  font-family: Georgia, "Times New Roman", Times, serif;
}
.container-mt-5 h2 {
  font-size: 2rem;
  font-weight: 400;
  color: #412277;
}
input {
  outline: none;
  border: none;
}
input {
  display: block;
  width: 100%;
  font-size: 18px;
  background: transparent;
  color: #0f0f0f;
  font-weight: 500;
  padding: 10px 0px;
}
.form-group-form-button {
  margin-top: 9px;
  width: 150px;
  height: 25px;
  border-radius: 5px;
  outline: none;
  font-weight: 500;
  font-size: 1rem;
  border: none;
  background: #445a94;
  color: #fff;
  box-shadow: 8px 10px 30px rgba(0, 0, 0, 0.2);
  transition: 0.4s;
  text-align: center;
}
.form-group-form-button:hover {
  background: #4b2888;
  color: #fff;
}
form-group {
  width: 80%;
  position: relative;
  border-bottom: 2px solid #b2b2b2;
  margin-bottom: 13px;
}
.container-mt-5 {
  width: 80%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  height: 90vh;
  background-color: #f8f9fb;
  display: flex;
  box-shadow: 5px 10px 38px rgba(0, 0, 0, 0.2);
}
